<template>
    <div class="box">
        <el-main>
           <div class="table-header-btn">
                <div class="table-header-btn-l"></div>
                <div class="table-header-btn-r">
                    <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
                    <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
                    <el-button type="primary" size="small"  @click="saveMetaSort">保存</el-button>
                    <el-button type="primary" size="small" @click="addListClick()">选择列</el-button>
                </div>   
            </div>
            <el-table 
                ref="multipleTable" 
                :data="tableData" 
                @selection-change="selectionChangeHandle"
            >
                <el-table-column type="selection" width="50" align="center"></el-table-column>
                <el-table-column prop="colCode" label="列名" ></el-table-column>
                <el-table-column prop="orderType" label="排序方式">
                    <template slot-scope="scope">
                        <el-select v-model="scope.row.orderType" placeholder="请选择" clearable>
                            <el-option
                                v-for="item in orderType"
                                :key="item.value"
                                :label="item.label"
                                :value="item.label"
                            >
                            </el-option>
                        </el-select>
                    </template>
                </el-table-column> 
            </el-table>
        </el-main>
        <el-dialog 
            title="选择列设置" 
            :visible.sync="sortSetDialogVisable" 
            width="50%" 
            v-if="sortSetDialogVisable" 
            @close='closeDialog' 
        >
            <sortSetDialog :checkedData="checkedData"></sortSetDialog>
        </el-dialog>
        
    </div>
</template>
<script>
    import {swapItems} from '@/utils';
    import sortSetDialog from './sort-set-dialog.vue';
    export default{
        components:{
            sortSetDialog
        },
        data(){
            return{
                tableData:[],
                sortSetDialogVisable:false,
                orderType:[
                    {
                        value: '1',
                        label: '升序'
                    }, 
                    {
                        value: '2',
                        label: '降序'
                    }, 
                ],
                treeData:[],
            }
        },
        created(){
            
        },
        methods:{
            getDataList(data){
                this.treeData = data;
                this.$http({
                    url: this.$http.adornUrl(this.API.getMingceConditionsOrder),
                    method: 'post',
                    data: this.$http.adornData({
                        mingceId:data.id
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.tableData = data.data;
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            },
            addListClick(){
                this.checkedData = this.treeData;
                this.sortSetDialogVisable = true;
            },
            closeDialog() {
                this.getDataList(this.checkedData)
            },
            selectionChangeHandle(val) {//多选
                this.tableDataSelections = val;
            },
            moveDirection(direction){ //移动
                if(this.tableDataSelections.length === 0) {
                    this.$confirm('请选择一条数据！', '提示', {
                        confirmButtonText: '确定',
                        showCancelButton: false,
                        type: 'warning',
                        center: true
                    });
                    return;
                };
                var that=this,index;
                that.tableDataSelections.sort((prev,next)=>{
                    return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
                })
                if(direction=='down') that.tableDataSelections.reverse()
                that.tableDataSelections.forEach(item=>{
                    index=that.tableData.indexOf(item); //得到右侧选中值的标识坐标
                    if(direction=='up') that.moveUp(index);
                    else if(direction=='down') that.moveDown(index)
                })
            },
            moveUp(index){//上移
                if(index==0) return;
                this.tableData=swapItems(this.tableData,index,index-1,'up');
            },
            moveDown(index){ //下移
                if(index==this.tableData.length-1) return;
                this.tableData=swapItems(this.tableData,index,index+1,'down');
            },
            saveMetaSort(){//保存排序
                var sortArr=[];
                if(this.tableData.length<0) return;
                this.tableData.forEach((item,index)=>{
                    sortArr.push({
                        orderId:item.orderId,
                        orderno:index+1,
                        mingceId:item.mingceId,
                        orderType:item.orderType,
                        colCode:item.colCode,
                        tabCode:item.tabCode,
                    })
                })
                this.$http({
                    url: this.$http.adornUrl(this.API.updateMingceOrder),
                    method: 'post',
                    data: this.$http.adornData({
                        data:JSON.stringify(sortArr)
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.$message.success('操作成功');
                        // this.$emit("addback",true);
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            },
        }
    }
</script>
<style lang="scss" scoped>
    .el-main {
        padding:0;
    }
</style>


